/* .feeadback__checbox-label:hover {
  color: #663d15;
} */
.feeadback__checbox-label input[type="checkbox"] + .feeadback__checbox-indicator {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 18px;
  height: 18px;
  border: 2px solid #E3B873;
  border-radius: 1px; }

.feeadback__checbox-label input[type="checkbox"]:focus + .feeadback__checbox-indicator {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color; }

.feeadback__checbox-label input[type="checkbox"]:checked + .feeadback__checbox-indicator::before,
.feeadback__checbox-label input[type="checkbox"]:checked + .feeadback__checbox-indicator::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 2px;
  width: 14px;
  height: 2px;
  background-color: #ffffff; }

.feeadback__checbox-label input[type="checkbox"]:checked + .feeadback__checbox-indicator::before {
  transform: rotate(45deg); }

.feeadback__checbox-label input[type="checkbox"]:checked + .feeadback__checbox-indicator::after {
  transform: rotate(-45deg); }

