  font-size: 0;
  border: 0;
  outline: none;
  border-radius: 50%;
  border: 2px solid #ffffff;
  background-color: inherit;
  cursor: pointer; }

.modal__close::before,
.modal__close::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 3px;
  width: 12px;
  height: 2px;
  background-color: #ffffff; }

.modal__close::before {
  transform: rotate(45deg); }

.modal__close::after {
  transform: rotate(-45deg); }

