.btn {
	display: inline-block;
	font-family: FuturaDemiC;
	margin: 0;
	color: #ffffff;
	background: #E3B873;
	box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.2);
	border-radius: 2px;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
}

.btn:hover {
	background: #E3B873;
	box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.2);
}

.btn--request {
	position: absolute;
	margin-top: 0;
	bottom: -25px;
}